// edited jobsheet checker
export const jobSheetChecker = ({ moduleEntry }) => {
    let edited = false;
    const a = moduleEntry?.jobPost?.list?.map(item => {
        const check = getEditedData({ data: item })
        if (check) edited = true
    })
    return edited
}

export const totalQty = (qty) => {
    if (qty) {
        if (Number.isInteger(qty)) return qty
        else return Number.parseFloat(qty).toFixed(2)
    }
    else return 0
}

export const checkisEmpty = (moduleEntry) => {
    let isRequired = false
    moduleEntry.poList.map(po => {
        po.itemList.map(item => {
            if (item.qtyIsEmpty || item.noteIsEmpty || item.outboundTypeIsEmpty || item.ripenessIsEmpty || item.gradeIsEmpty || item.packIsEmpty) isRequired = true
        })
    })
    return isRequired
}

// check loading
export const responseCheck = ({ isLoading, summaryData = null }) => {
    if (!summaryData && !isLoading) {
        return true
    }
    if (isLoading) {
        return false
    }
    else {
        return true
    }
}

