//get initial jobsheet
export const getInitialJs = ({ moduleEntry }) => {
    return moduleEntry.jobPost.list.length
}

//remove sku
export const removeSku = async ({ dispatch, moduleEntry, moduleName }) => {
    let jobSheet = { ...moduleEntry }

    const list = moduleEntry.jobPost.list.filter(data => { if (data.checked) return data })
    jobSheet.jobPost.list = list
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: jobSheet })
    if (list.length === 0) removeEntryModule({ id: jobSheet.jobPost.id, moduleName, dispatch })
}

export const resetSKU = ({ dispatch, moduleSummary, moduleName }) => {
    let jobSheet = { ...moduleSummary }
    jobSheet.jobPost.list.map(list => {
        list.checked = true
    })
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: jobSheet })
}

export const setAreaId = ({ dispatch, selected }) => {
    if (selected) dispatch({ type: SET_AREA, areaId: selected })
}

export const countQty = ({ activeSku, moduleEntry, poListIdx, entryListIdx }) => {
    let number = parseInt(0)
    const list = moduleEntry.jobPost.list[activeSku].poList[poListIdx].itemList
    list.map((item, idx) => {
        if (idx !== entryListIdx) number += Number.isInteger(item?.qty) ? item?.qty : parseFloat(item?.qty)
    })

    return number
}

export const deleteRow = ({
    dispatch,
    moduleEntry,
    moduleName,
    activeSku,
    poListIdx,
    entryListIdx,
    e,
    countQty
}) => {
    let js = { ...moduleEntry }
    const number = countQty({ moduleEntry, activeSku, poListIdx, entryListIdx })
    const update = js.jobPost.list[activeSku].poList[poListIdx].initialWeight

    js.jobPost.list[activeSku].poList[poListIdx].initialWeightScreenUpdate = update - number
    js.jobPost.list[activeSku].poList[poListIdx].itemList.splice(entryListIdx, 1)

    if (js.jobPost.list[activeSku].poList[poListIdx].itemList.length === 0) js.jobPost.list[activeSku].poList[poListIdx].edited = false
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
}

export const setZeroQuantity = ({ dispatch, zeroQuantity }) => {
    dispatch({ type: 'SET_ZERO_QUANTITY', data: !zeroQuantity })
}

export const setMovementTargetRepack = ({ dispatch, stockMovementTargetRepack }) => {
    dispatch({ type: 'SET_MOVEMENT_TARGET_REPACK', data: !stockMovementTargetRepack })
}

export const setSwitch = ({ moduleName, search, updateList, dispatch, zeroQuantity, stockMovementTargetRepack, isMovement }) => {
    if (!isMovement) setZeroQuantity({ dispatch, zeroQuantity })
    if (isMovement) setMovementTargetRepack({ dispatch, stockMovementTargetRepack })
    if (search) updateList(!zeroQuantity, !stockMovementTargetRepack, moduleName)
}

export const countPack = ({ activeSku, moduleEntry, grade, slot }) => {
    let number = 0
    const list = moduleEntry.jobPost.list[activeSku].poList[0].itemList
    list.map((item) => {
        if (item?.pack && item.grade.label === grade && item.slot?.value == slot) number += item?.pack
    })
    return number
}

export const countPackMovement = ({ activeSku, moduleEntry, grade, slot }) => {
    let number = 0
    const list = moduleEntry.jobPost.list[activeSku].poList
    list.map(po => {
        po.itemList.map(item => {
            if (item?.qty && item.grade.label === grade) number += item?.qty
        })
    })
    return number
}


export const filterTargetRepack = (targetRepacks, gradeList, activeSku, moduleEntry, slot, targetMovement) => {
    const width = document.documentElement.clientWidth
    const colWidth = width <= 500 ? 100 : 'auto'
    let id = gradeList?.map(data => data.value)
    let data = targetRepacks?.map((data, idx) => {
        if (id.includes(data.skuUuid)) {

            // set grade
            let grade = null
            gradeList.map(gr => {
                if (gr.value === data.skuUuid) grade = gr.label
            })

            // set percent
            const totalPack = (targetMovement ? data?.totalMove : data?.totalPack) + (targetMovement ? countPackMovement({ activeSku, moduleEntry, grade, slot: data?.slot }) : countPack({ activeSku, moduleEntry, grade, slot: data?.slot }))
            const targetUom = data.targetUom
            const targetUos = data.targetUos
            const percent = (totalPack / (targetMovement ? targetUos : targetUom)) * 100

            return {
                warehouse: data.warehouse,
                skuUuid: data.skuUuid,
                skuName: data.skuName,
                targetDate: data.targetDate,
                targetUom: data.targetUom,
                targetUos: data.targetUos,
                slot: data.slot,
                totalPack: totalPack,
                totalMove: data.totalMove,
                no: idx + 1,
                grade: grade,
                [targetMovement ? '%Moved' : '%Repack']: `${percent?.toFixed(2)}%`,
            }
        }
    })
    data = data?.filter(data => data !== undefined)
    if (slot?.length) {
        const slotList = slot.map(s => Number.parseInt(s?.value))
        if (!slotList.includes(null)) data = data.filter(data => {
            return slotList.includes(Number.parseInt(data.slot))
        })
    }
    data = data?.map((data, idx) => {
        data.no = idx + 1
        return data
    })
    if (!data) data = []
    const repack = [
        { Header: 'Slot', placeholder: 'Slot', accessor: 'slot', width: 70, style: { textAlign: 'left', display: 'end', justifyContent: 'flex-end' } },
        { Header: 'Grade', placeholder: 'Grade', accessor: 'grade', width: 250 },
        { Header: 'Total Pack', placeholder: 'Total Pack', accessor: 'totalPack', style: { textAlign: 'right', display: 'flex', justifyContent: 'flex-end' }, width: colWidth, },
        { Header: 'Target Pack', placeholder: 'Target Pack', accessor: 'targetUom', width: colWidth, style: { textAlign: 'right !important', display: 'flex', justifyContent: 'flex-end' }, className: 'flex-adjust', headerClassName: 'flex-adjust' },
        { Header: '%Repack', placeholder: '%Repack', accessor: '%Repack', width: colWidth, style: { textAlign: 'right', display: 'flex', justifyContent: 'flex-end' }, },
    ]

    const move = [
        // { Header: 'Slot', placeholder: 'Slot', accessor: 'slot', width: 70, style: { textAlign: 'left', display: 'end', justifyContent: 'flex-end' } },
        { Header: 'Grade', placeholder: 'Grade', accessor: 'grade', width: 220, style: { textAlign: 'left', display: 'flex', justifyContent: 'flex-end' }, className: 'flex-adjust', headerClassName: 'flex-adjust' },
        { Header: 'Total Movement', placeholder: 'Total Movement', accessor: 'totalPack', width: colWidth, style: { textAlign: 'right !important', display: 'flex', justifyContent: 'flex-end' }, className: 'flex-end !important', headerClassName: 'flex-end !important' },
        // { Header: 'Total Movement', placeholder: 'Total Movement', accessor: 'totalPack', style: { textAlign: 'left', display: 'flex', justifyContent: 'flex-end' }, width: colWidth, },
        { Header: 'Target Movement', placeholder: 'Target Movement', accessor: 'targetUos', width: colWidth, style: { textAlign: 'right !important', display: 'flex', justifyContent: 'flex-end' }, className: 'flex-adjust', headerClassName: 'flex-adjust' },
        { Header: '%Moved', placeholder: '%Moved', accessor: '%Moved', width: colWidth, style: { textAlign: 'right', display: 'flex', justifyContent: 'flex-end' }, },
    ]
    let table = { data, columns: targetMovement ? move : repack }
    return table
}

