export const setTargetRepack = ({ moduleEntry, dispatch }) => {
    let updatedTargetRepacks = []
    moduleEntry.jobPost.list.map(ls => {
        if (ls.poList[0].targetRepacks?.length) updatedTargetRepacks = updatedTargetRepacks.concat(ls.poList[0].targetRepacks)
    })
    dispatch({ type: 'IS_LOADING', targetRepacks: updatedTargetRepacks })
}

export const addSlot = ({ dispatch, moduleEntry, slot, moduleName }) => {
    let newModuleEntry = { ...moduleEntry }

    if (slot?.length) {
        newModuleEntry.jobPost.slot = slot.map(s => {
            if (Number.isInteger(s?.value)) return Number.parseInt(s?.value)
            else return 0
        })
        dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: newModuleEntry })
    }
}