//get user credential by email
export const getUserData = async ({ dispatch, sessionId, setComplete }) => {
  try {
    dispatch({ type: GET_USER, status: null });
    const url = `user/validate?sessionId=${sessionId}`;
    const { data, headers } = await axios(url);
    setComplete(true);
    if (data) {
      data["sessionId"] = headers["session-id"];

      if (data?.isSuccess)
        dispatch({ type: GET_USER, user: data, status: null });
      else
        dispatch({
          type: GET_USER,
          user: null,
          status: { message: "Unauthorized", code: 401 },
          statusMessage: data.message,
        });
    }
    return true;
  } catch (e) {
    const status = throwErrorStatus(e);
    setComplete(true);
    dispatch({
      type: "STATUS",
      status,
      user: null,
    });
  }
};

//logout
export const userLogout = async ({ dispatch, email }) => {
  try {
    const logout = `/user/signOut?email=${email}`;
    await axios(logout);
    dispatch({ type: USER_LOGOUT, user: null });
    window.location.replace("https://dev-wms-web.sayurbox.tech/");
    return true;
  } catch (error) { }
};

export const checkSessionId = async ({ dispatch, sessionId }) => {
  try {
    const url = `/user/checkSession?sessionId=${sessionId}`;
    const data = await axios(url);
    if (data) {
      if (!data?.data?.isSuccess) dispatch({ type: USER_LOGOUT, user: null });
    }
  }
  catch (e) {
    const status = throwErrorStatus(e)
    dispatch({
      type: GET_USER,
      user: null,
      status: { message: "Unauthorized", code: 401 },
      statusMessage: status.message,
    });
  }
};

// get by po sku code

export const getPoBySkuCode = async ({
  moduleName,
  setModule,
  dispatch,
  poNumber,
  warehouseUuid,
  areaId,
  areaName,
  setSkuList,
  setLocalEntryData
}) => {
  try {
    let param = ''
    if (poNumber.length > 0) param = `&wmsPoSkuCode=${poNumber}`;
    if (warehouseUuid) param = param + `&warehouseUuid=${warehouseUuid}`;
    if (areaId) param = param + `&areaId=${areaId?.value}`;
    let url = `/stocktake/poByPoSkuCode? ${param}`;
    const { data, status } = await axios(url);
    if (data?.data?.length) {
      let entryData = {}
      entryData.jobPost = {}
      entryData.jobPost.list = []
      const wait = data.data.map(async data => {
        const {
          ripeness,
          expiryDate,
          codeGrade,
          inboundDate,
          inboundQty,
          initialWeight,
          photo,
          skuName,
          skuUuid,
          skuNumber,
          supplierId,
          supplierName,
          totalPack,
          uom,
          wmsPoSkuCode,
          slot,
          category
        } = data
        const grade = await getGrade({ skuId: skuUuid })
        let ddGrade = grade?.filter(data => data.label === skuName)
        ddGrade?.length ? ddGrade = ddGrade[0] : ddGrade = { label: null, value: null }
        entryData.jobPost.id = 0
        entryData.jobPost.warehouseSource = warehouseUuid
        entryData.jobPost.warehouseDestination = warehouseUuid
        entryData.jobPost.areaSource = areaId?.value
        entryData.jobPost.areaName = areaId?.label
        entryData.jobPost.areaDestination = areaId?.value
        if (entryData?.jobPost?.list?.length) {
          const idx = entryData?.jobPost.list?.findIndex((data, idx) => data?.skuName === skuName)
          if (idx === -1) {
            entryData.jobPost.list.push(
              {
                category: category,
                packSize: totalPack,
                checked: true,
                gradeList: grade,
                slot: slot,
                skuUuid: skuUuid,
                skuNumber: skuNumber,
                skuName: skuName,
                warehouseId: warehouseUuid,
                unitOfMeassures: uom,
                totalQty: initialWeight,
                photo: photo,
                poList: [
                  {
                    wmsPoSkuCode: wmsPoSkuCode,
                    skuUuid: skuUuid,
                    inboundDate: inboundDate,
                    initialWeight: initialWeight,
                    uom: uom,
                    expiryDate: expiryDate,
                    supplierId: supplierId,
                    supplierName: supplierName,
                    codeGrade: codeGrade,
                    inboundQty: inboundQty,
                    totalPack: totalPack,
                    category: category,
                    itemList: [
                      {
                        qty: initialWeight,
                        grade: ddGrade,
                        ripeness: {
                          label: ripeness,
                          value: ripeness
                        },
                        expiryDate: expiryDate,
                        expiryDateText: dateDMY({ date: expiryDate }),
                      }
                    ]
                  }
                ]
              }
            )
          }
          if (idx >= 0) {
            entryData.jobPost.list[idx].totalQty += initialWeight
            entryData.jobPost.list[idx].poList.push(
              {
                wmsPoSkuCode: wmsPoSkuCode,
                skuUuid: skuUuid,
                inboundDate: inboundDate,
                initialWeight: initialWeight,
                uom: uom,
                expiryDate: expiryDate,
                supplierId: supplierId,
                supplierName: supplierName,
                codeGrade: codeGrade,
                inboundQty: inboundQty,
                totalPack: totalPack,
                category: category,
                itemList: [
                  {
                    qty: initialWeight,
                    grade: ddGrade,
                    ripeness: {
                      label: ripeness,
                      value: ripeness
                    },
                    expiryDate: expiryDate,
                    expiryDateText: dateDMY({ date: expiryDate }),
                  }
                ]
              }
            )
          }
        }

        if (!entryData?.jobPost?.list?.length) {
          entryData.jobPost.list.push(
            {
              checked: true,
              category: category,
              packSize: totalPack,
              gradeList: grade,
              skuUuid: skuUuid,
              skuNumber: skuNumber,
              skuName: skuName,
              warehouseId: warehouseUuid,
              unitOfMeassures: uom,
              totalQty: initialWeight,
              photo: photo,
              poList: [
                {
                  wmsPoSkuCode: wmsPoSkuCode,
                  skuUuid: skuUuid,
                  inboundDate: inboundDate,
                  initialWeight: initialWeight,
                  uom: uom,
                  expiryDate: expiryDate,
                  supplierId: supplierId,
                  supplierName: supplierName,
                  codeGrade: codeGrade,
                  inboundQty: inboundQty,
                  totalPack: totalPack,
                  category: category,
                  itemList: [
                    {
                      qty: initialWeight,
                      grade: ddGrade,
                      ripeness: {
                        label: ripeness,
                        value: ripeness
                      },
                      expiryDate: expiryDate,
                      expiryDateText: dateDMY({ date: expiryDate }),
                    }
                  ]
                }
              ]
            }
          )
        }
      })
      await Promise.all(wait)
      await setLocalEntryData(entryData)
    }
  }
  catch (e) {
    console.log(e);
  }
}

// get stock take summary data
export const getStockTakeSummary = async ({
  stockMovementTargetRepack,
  moduleName,
  zeroQuantity,
  singleView,
  module,
  setModule,
  dispatch,
  desc,
  warehouseUuid,
  areaId,
  areaIdDestination,
  transaction,
}) => {
  try {
    let param = "";
    // let cancelToken = axios.CancelToken.source();
    if (desc.length > 0) param = `&desc=${desc}`;
    if (warehouseUuid) param = param + `&warehouseUuid=${warehouseUuid}`;
    if (areaId) param = param + `&areaId=${areaId?.value}`;
    if (transaction === 'waste') param = param + `&transaction=${transaction}`
    if (areaIdDestination === 'movement') param = param + `&areaId=${areaIdDestination?.value}`
    if (moduleName === 'stockMovement' && stockMovementTargetRepack) param = param + `&haveTargetMovement=${stockMovementTargetRepack}`
    let url = `oss/skus? ${param}`;

    dispatch({ type: GET_STOCK_TAKE_ACTION, isLoading: true });
    setModule(dt);
    const { data, status } = await axios(url);
    let dt = [];
    // filter selected sku
    const skuList = module?.jobPost?.list
    if (!data?.data) dispatch({ type: GET_STOCK_TAKE_ACTION, isLoading: false });
    let tmp = data?.data.map(d => {
      //if outbound, qty < 0 then skip
      if (isFloat(d.totalQty) < 0.01) { }
      if (isFloat(d.totalQty) <= 0 && zeroQuantity && moduleName === 'stockTake') {
        let isExist = skuList?.filter(sku => sku.skuUuid === d.skuUuid)
        if (singleView) isExist = []
        dt.push({
          targetMovementProgress: d.targetMovementProgress?.length ? d.targetMovementProgress : [],
          checked: isExist?.length > 0 ? true : false,
          gradeList: d.gradeList,
          photo: d.photo,
          poList: d.poList,
          packSize: d.packSize,
          category: d.category,
          skuName: d.skuName,
          skuNumber: parseInt(d.skuNumber),
          skuUuid: d.skuUuid,
          totalQty: isFloat(d.totalQty),
          unitOfMeassures: d.unitOfMeassures,
          warehouseId: d.warehouseId,
          originalUom: d.originalUom,
        });
      }
      if (isFloat(d.totalQty) > 0 && (moduleName === 'stockTake' ? !zeroQuantity : true)) {
        let isExist = skuList?.filter(sku => sku.skuUuid === d.skuUuid)
        if (singleView) isExist = []
        dt.push({
          targetMovementProgress: d.targetMovementProgress?.length ? d.targetMovementProgress : [],
          checked: isExist?.length > 0 ? true : false,
          gradeList: d.gradeList,
          photo: d.photo,
          poList: d.poList,
          packSize: d.packSize,
          category: d.category,
          skuName: d.skuName,
          skuNumber: parseInt(d.skuNumber),
          skuUuid: d.skuUuid,
          totalQty: isFloat(d.totalQty),
          unitOfMeassures: d.unitOfMeassures,
          warehouseId: d.warehouseId,
          originalUom: d.originalUom,
        });
      }
    });
    setModule(dt);
    dispatch({ type: GET_STOCK_TAKE_ACTION, isLoading: false });
  } catch (e) { }
};

