import axios from "axios";
import { WAREHOUSE_AREA, RIPENESS_LEVEL, GRADE, OUTBOUND_TYPE } from "../action";

export const getWarehouseArea = async ({
  warehouseUuid,
  dispatch,
  type = "dropdown",
}) => {
  try {
    let url = `/inventory/warehouseArea?warehouseUuid=${warehouseUuid}`;
    const { data = null } = await axios(url);
    if (type == "csvValidator") {
      //get warehouse area for csv validator
      dispatch({
        type: WAREHOUSE_AREA,
        warehouseAreaCsv: data?.data.map((data) => ({
          label: data?.areaName,
          value: data?.id,
        })),
      });
    } else {
      return data?.data.map((data) => ({
          label: data?.areaName,
          value: data?.id,
        }))
    }
  } catch (e) {
    console.log(e);
  }
};

export const getRipenessLevel = async ({ dispatch }) => {
  try {
    const url = `/inventory/ripenessLevel`;
    const { data } = await axios(url);
    const ddList = data?.data.map((data, i) => ({ label: data, value: String(data) }))
    dispatch({
      type: RIPENESS_LEVEL,
      ripenessLevel: ddList
    });
    return ddList
  } catch (e) {
    console.log(e);
  }
};

export const getGrade = async ({ skuId,dispatch }) => {
  try {
    const url = `/inventory/grade?skuId=${skuId}`;
    const { data } = await axios(url);
    // console.log(data);
    if (data) {
      return data.data.map((data) => ({
        label: data.skuDescription,
        value: data.id,
        multiplier:data.parentToChildMultiplier,
        uom:data.uom
      }));
    }
  } catch (e) {
    console.log(e);
  }
};

export const getOutboundType = async ({
  type = "dropdown",
  dispatch
}) => {
  try {
    const url = `/inventory/outboundType`;
    const { data } = await axios(url);
    let outboundType = []
    if (data) {
      if (type == 'csvValidator') {
        dispatch({
          type: OUTBOUND_TYPE,
          outboundTypeCsv: data?.data.map((listItem) => ({
            label: listItem.description,
            value: listItem.code
          })),
        });
      } else {
        outboundType = data.data.map(listItem => ({
          label: listItem.description,
          value: listItem.code
        }))
      }
    }
    return outboundType;
  } catch (e) {
    console.log(e);
  }
};

export const getWarehouse = async({}) => {
  try{
    const url = `/oss/warehouses`
  const {data} = await axios(url)
  if(data){
    return data?.data.map(data => ({label: data.name, value:data.id}))
  }
  }
  catch(e){
    console.log(e)
  }
}